import { memo, useCallback, useRef } from 'react';
import { I_Scene } from '@containers/Home/types';
import dynamic from 'next/dynamic';
import { useEventFloorplan } from '@containers/Home/hook/useEventFloorplan';
import { useTourEventState } from '@containers/Home/global/TourStats';
import { Type_RefFuncImageFloorplan } from './ImageFloorplan';

const Popup = dynamic(() => import('./Popup'));

interface Props {
  onChangeScene: (scene: I_Scene) => void;
}

const FloorplanPanel = memo(({ onChangeScene }: Props) => {
  const floorplanRef = useRef<Type_RefFuncImageFloorplan>(null!);
  const [tourEventStats, setTourEventStats] = useTourEventState('state');

  const {
    listFloorplan,
    markerSelected,
    floorplanSelected,
    configFloorplan,
    onChangeFloorplan,
  } = useEventFloorplan({
    floorplanRef,
  });

  const onClosePopup = useCallback(
    () => setTourEventStats(prev => ({ ...prev, isVisibleFloorplan: false })),
    [setTourEventStats],
  );

  return (
    !!tourEventStats.isVisibleFloorplan && (
      <Popup
        visible={tourEventStats.isVisibleFloorplan}
        listFloorplan={listFloorplan}
        markerSelected={markerSelected}
        floorplanSelected={floorplanSelected}
        configFloorplan={configFloorplan}
        onChangeFloorplan={onChangeFloorplan}
        onChangeScene={onChangeScene}
        onCancel={onClosePopup}
      />
    )
  );
});

export default FloorplanPanel;
